export const BASE_URL = "https://royalfurniture.herokuapp.com";

export const URLS = {
  api: {
    summary: {
      royal_daily: "/api/Royal%20Daily/summary",
      products_sold: "/api/Products%20Sold/summary",
    },
    report: {
      royal_daily: "/api/Royal%20Daily/report",
      products_sold: "/api/Products%20Sold/report",
    },
    pending: {
      royal_daily: "/api/Royal%20Daily/pending",
    },
  },
  report: "/getreport",
  add: "/add",
};

export function create_url(api_url, param = null, start = null, end = null) {
  if (start == null) start = new Date().toISOString().slice(0, 10);
  if (end == null) end = new Date().toISOString().slice(0, 10);
  let url = BASE_URL + api_url + "?" + "start=" + start + "&end=" + end;
  if (param != null) {
    const keys = Object.keys(param);
    keys.forEach((key, index) => {
      url += "&" + key + "=";
      if (Array.isArray(param[key])) {
        for (var i = 0; i < param[key].length; ++i) {
          url += param[key][i];
          url += i === param[key].length - 1 ? "" : ",";
        }
      } else {
        url += param[key];
      }
    });
  }
  return url;
}

