import { useContext, useState, useEffect } from "react";
import { UserContext } from "../../components/UserContext";
import { Calender } from "../../components/calender/Calender";
import { Spinner } from "../../components/spinner/Spinner";
import { Error } from "../../components/error/Error";
import RoyalDaily from "../../components/sheets/RoyalDaily";
import { create_url, royal_summary_param, URLS } from "../url";
import { DailySummary } from "../../components/sheets/Summary";
import { Tarekh } from "../../components/date/Tarekh";

const Report = () => {
  const { date, param, loading, setLoading } = useContext(UserContext);
  const [error, setError] = useState(null);
  const [royalDaily, setRoyalDaily] = useState(null);

  useEffect(() => {
    setError(null);
    Promise.all([
      fetch(
        create_url(URLS.api.report.royal_daily, param, date["from"], date["to"])
      ).then((response) => {
        if (response.ok) {
          return response.json();
        }
        throw response;
      }),
      fetch(
        create_url(
          URLS.api.summary.royal_daily,
          royal_summary_param,
          date["from"],
          date["to"]
        )
      ).then((response) => {
        if (response.ok) {
          return response.json();
        }
        throw response;
      }),
    ])
      .then(([report, summary]) => {
        setRoyalDaily({
          sheet: JSON.parse(report.data),
          summary: JSON.parse(summary.data),
        });
      })
      .catch((error) => {
        console.error("Error Fetching data: ", error);
        setError(error);
      })
      .finally(() => {
        setLoading(false);
      });
  }, [date, param, setLoading]);

  return (
    <div>
      {loading ? (
        <Spinner />
      ) : (
        <div>
          {error ? (
            <Error />
          ) : (
            <div>
              {royalDaily !== null ? (
                <div>
                  <Tarekh from={date["from"]} to={date["to"]} />
                  <DailySummary data={royalDaily.summary} />
                  {royalDaily.sheet.length !== 0 ? (
                    <RoyalDaily sheet={royalDaily.sheet} />
                  ) : (
                    <div> Nothing to see</div>
                  )}
                </div>
              ) : (
                <div>Noting[:]</div>
              )}
            </div>
          )}
        </div>
      )}
    </div>
  );
};

const Filter = () => {
  //To be implemmented
  const { param, setParam } = useContext(UserContext);
  return <div></div>;
};

const DailyReport = () => {
  const [date, setDate] = useState({
    from: null,
    to: null,
  });
  const [loading, setLoading] = useState(true);
  const [param, setParam] = useState(null);

  return (
    <div>
      <UserContext.Provider
        value={{ date, setDate, loading, setLoading, param, setParam }}
      >
        <Calender />
        <Filter />
        <Report />
      </UserContext.Provider>
    </div>
  );
};

export default DailyReport;
