import { useState, useEffect } from "react";
import { BASE_URL, URLS } from "../url";
import { Spinner } from "../../components/spinner/Spinner";
import { Error } from "../../components/error/Error";
import Pending from "../../components/sheets/Pending";

const PendingReport = () => {
  const [pendingReport, setPendingReport] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);
  useEffect(() => {
    fetch(BASE_URL + URLS.api.pending.royal_daily)
      .then((res) => {
        if (res.ok) return res.json();
        throw res;
      })
      .then((report) => {
        setPendingReport(JSON.parse(report.data));
      })
      .catch((error) => {
        console.error("Error Fetch data: ", error);
        setError(error);
      })
      .finally(() => {
        setLoading(false);
      });
  }, []);
  return (
    <div>
      {loading ? (
        <Spinner />
      ) : (
        <div>
          {error ? (
            <Error />
          ) : (
            <div>
              {pendingReport !== null ? (
                <div>
                  <Pending sheet={pendingReport} />
                </div>
              ) : (
                <div>Noting to see</div>
              )}
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default PendingReport;
