import { useState, useEffect } from "react";
import { Link } from "react-router-dom";

import { Spinner } from "../../components/spinner/Spinner";
import { Error } from "../../components/error/Error";
import {
  royal_summary_param,
  products_summary_param,
  money_spent_summary_param,
  URLS,
  create_url,
} from "../url";
import { Tarekh } from "../../components/date/Tarekh";
import {
  DailySummary,
  ProductSummary,
  MoneySpent,
} from "../../components/sheets/Summary";

const Home = () => {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);
  const [data, setData] = useState(null);
  const today = new Date().toISOString().slice(0, 10);

  useEffect(() => {
    Promise.all([
      fetch(create_url(URLS.api.summary.royal_daily, royal_summary_param)).then(
        (response) => {
          if (response.ok) return response.json();
          throw response;
        }
      ),
      fetch(
        create_url(URLS.api.summary.products_sold, products_summary_param)
      ).then((response) => {
        if (response.ok) return response.json();
        throw response;
      }),
      fetch(
        create_url(URLS.api.summary.royal_daily, money_spent_summary_param)
      ).then((response) => {
        if (response.ok) return response.json();
        throw response;
      }),
    ])
      .then(([royal_daily, products_sold, money_spent]) => {
        setData({
          royal_daily: JSON.parse(royal_daily.data),
          products_sold: JSON.parse(products_sold.data),
          money_spent: JSON.parse(money_spent.data),
        });
      })
      .catch((error) => {
        console.error("Error Fetching data: ", error);
        setError(error);
      })
      .finally(() => {
        setLoading(false);
      });
  }, []);

  return (
    <div>
      {loading ? (
        <Spinner />
      ) : (
        <div>
          {error ? (
            <Error />
          ) : (
            <div>
              <Tarekh from={today} to={today} />
              <Link
                to={{
                  pathname: "/dailyreport",
                }}
              >
                <DailySummary data={data["royal_daily"]} />
              </Link>
              <Link
                to={{
                  pathname: "/profitreport",
                }}
              >
                <ProductSummary data={data["products_sold"]} />
              </Link>
              <Link
                to={{
                  pathname: "/karchoreport",
                }}
              >
                <MoneySpent data={data["money_spent"]} />
              </Link>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default Home;
