import { useContext } from "react";
import { UserContext } from "../UserContext";
export const Calender = () => {
  const handleOnChange = (e) => {
    setLoading(true);
    const id = e.target.id;
    const value = e.target.value;
    setDate({
      ...date,
      [id]: value,
    });
  };
  const { date, setDate, setLoading } = useContext(UserContext);
  return (
    <div>
      <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
        <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
          From
        </label>
        <input
          className="appearance-none block w-full bg-gray-200 text-gray-700 border border-yellow-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
          onInput={handleOnChange}
          type="date"
          id="from"
          value={date.from ? date.from : ""}
        />
      </div>
      <div className="w-full md:w-1/2 px-3 mb-6 md:mb-0">
        <label className="block uppercase tracking-wide text-gray-700 text-xl font-bold mb-2">
          To
        </label>
        <input
          className="appearance-none block w-full bg-gray-200 text-gray-700 border border-yellow-500 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white"
          onInput={handleOnChange}
          type="date"
          id="to"
          value={date.to ? date.to : ""}
        />
      </div>
    </div>
  );
};
