export const Tarekh = ({ from, to }) => {
  if (from === undefined) {
    from = new Date().toISOString().slice(0, 10);
  }
  if (to === undefined) {
    to = new Date().toISOString().slice(0, 10);
  }
  return (
    <div className="flex justify-center items-center text-2xl md:text-4xl text-gray-700 ">
      {from === to ? (
        <div> {from}</div>
      ) : (
        <div>
          {" "}
          {from} to {to}{" "}
        </div>
      )}
    </div>
  );
};
