import "./assets/css/index.css";
import Navbar from "./components/navbar/NavBar";
import DailyReport from "./pages/dailyreport/DailyReport";
import Home from "./pages/home/Home";
import ProfitReport from "./pages/profitReport/ProfitReport";
import KharchoReport from "./pages/kharchoReport/KharchoReport";
import PendingReport from "./pages/pendingReport/PendingReport";
import {
  RoyalDaily,
  ProductsSold,
  CustomerDetail,
} from "./components/forms/Form";

import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

function App() {
  return (
    <div>
      <Router>
        {" "}
        <div className="App">
          <Navbar />
          <Switch>
            <Route exact path="/RoyalFurnitures" component={Home}></Route>
            <Route path="/dailyreport" component={DailyReport}></Route>
            <Route path="/pendingreport" component={PendingReport}></Route>
            <Route path="/profitreport" component={ProfitReport}></Route>
            <Route path="/dailyappend" component={RoyalDaily}></Route>
            <Route path="/karchoreport" component={KharchoReport}></Route>
            <Route path="/productappend" component={ProductsSold}></Route>
            <Route path="/customerappend" component={CustomerDetail}></Route>
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default App;
