export function Summary({ sheet }) {
  console.log(sheet);
  var total = 0;
  sheet.map((data) => {
    total += Number(data["Pending"]);
  });

  return (
    <div>
      <div className="bg-white lg:w-1/3  rounded shadow-xl  border m-3 p-3 p-4 leading-normal">
        <div className="mb-8 ">
          <div className="text-gray-900 uppercase font-bold text-2xl mb-2">
            Pending Report
          </div>
          <p className="text-gray-700 leading-8 uppercase text-xl text-base">
            Total: Rs {total}
          </p>
        </div>
      </div>
    </div>
  );
}
